body {
  color: #333;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

header {
  text-align: center;
  background: #f4f4f4;
  padding: 1rem;
}

main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

footer {
  text-align: center;
  background: #f4f4f4;
  padding: 1rem;
}

.card-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.card {
  width: 300px;
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  gap: 10px;
  padding: 15px;
  display: flex;
  box-shadow: 0 4px 6px #0000001a;
}

.card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.card h2 {
  margin: 0;
  font-size: 1.2rem;
}

.card p {
  margin: 0;
  font-size: 1rem;
}

.modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #0006;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  width: 80%;
  max-width: 400px;
  background-color: #fefefe;
  border: 1px solid #888;
  margin: 15% auto;
  padding: 20px;
}

.close {
  color: #aaa;
  float: right;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.cookie-consent-banner {
  width: 100%;
  color: #fff;
  z-index: 1000;
  background-color: #333;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}

.cookie-consent-banner p {
  margin: 0;
}

.accept-cookies-btn {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  margin: 0 2rem 0 1rem;
  padding: 8px 16px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.hidden {
  display: none;
}

